        ctx.font = '700 35px Inter';
        ctx.fillStyle = '#0272FE';
        ctx.textAlign = 'left';
        ctx.textBaseline = 'middle';
        ctx.fillText(data.amount + ' VND', canvas.width - 605, 375 + top);

        //Số tiền bằng chữ
        ctx.font = '400 18px Inter';
