        ctx.fillStyle = '#687377';
        ctx.textAlign = 'left';
        ctx.textBaseline = 'middle';
        ctx.fillText(text_money, canvas.width - 605, 420 + top);


        //Tài khoản thụ hưởng
        ctx.font = '400 25px Inter';
        ctx.fillStyle = '#383B42';
        ctx.textAlign = 'left';
        ctx.textBaseline = 'middle';
        ctx.fillText(data.receiver_number, canvas.width - 430, 705 + top);

        //Tài khoản người gửi tiền
        ctx.font = '400 25px Inter';
