        ctx.fillStyle = '#000000';
        ctx.textAlign = 'left';
        ctx.textBaseline = 'middle';
        ctx.fillText(data.sender_number, canvas.width - 430, 560 + top);


        //Tên người thụ hưởng
        ctx.font = '600 30px Inter'; // Set the font style and size
        ctx.fillStyle = '#000000'; // Set the fill color
        ctx.textAlign = 'left'; // Set the text alignment
        ctx.textBaseline = 'middle'; // Set the vertical alignment
        ctx.fillText(data.receiver_name, canvas.width - 430, 650 + top);

