import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import MetisMenu from '@metismenu/react';
import AuthMoldel from '../../models/AuthMoldel';
import adminMenus from '../../route/adminMenu';
import menus from '../../route/menu';
import HomeModel from '../../models/HomeModel';

function AsideMenu(props) {
    const isAdmin = AuthMoldel.isAdmin()
    const isAdminPage = window.location.pathname.includes("admin");
    const [data, setData] = useState([]);
    const [showPackage,setShowPackage] = useState(0);

    const handleClick = () => {
        if (showPackage == 0) {
            setShowPackage(1);
        }else{
            setShowPackage(0);
        }
    }


    useEffect(() => {
        HomeModel.getInfoHeader().then((res) => {
            setData(res.data);
        }).catch((err) => {
        })
    }, [])

    return (
        <MetisMenu className="metismenu" id="menu">
            <li className='info-user-sidebar ms-5'>
                    <span className="nav-text text-fm-sd">Số dư : {data.account_balance ? data.account_balance.toLocaleString('en-US') : data.account_balance} VNĐ</span> <br/>
                    <span className="nav-text text-fm-sd">Tài khoản giới thiệu : {data.referral_account_balance ? data.referral_account_balance.toLocaleString('en-US') : data.referral_account_balance} VNĐ</span>
            </li>
            {
                !isAdminPage ? menus.map((menu, key) => (
                    <li key={key}>
                        {menu.id == 'package' ?
                            (
                                <>
                                    <Link onClick={handleClick}>
                                        <div className="menu-icon" dangerouslySetInnerHTML={{ __html: menu.icon }}></div>
                                        <span className="nav-text">{menu.text}</span>
                                    </Link>
                                    {
                                        showPackage == 1 &&
                                        (
                                            <>
                                                <div className='row ms-4'>
                                                    <Link to="/package/VCB">
                                                        <span className="nav-text"> - Gói Vietcombank</span>
                                                    </Link>
                                                </div>
                                                <div className='row ms-4'>
                                                    <Link to="/package/TCB">
                                                        <span className="nav-text"> - Gói Techcombank</span>
                                                    </Link>
                                                </div>
                                                <div className='row ms-4'>
                                                    <Link to="/package/MB">
                                                        <span className="nav-text"> - Gói MB-bank</span>
                                                    </Link>
                                                </div>
                                            </>
                                        )
                                    }
                                </>
                            ):
                            (
                                <Link to={menu.path}>
                                    <div className="menu-icon" dangerouslySetInnerHTML={{ __html: menu.icon }}></div>
                                    <span className="nav-text">{menu.text}</span>
                                </Link>
                            )
                        }
                    </li>
                )) : null
            }
            {
                isAdminPage ? adminMenus.map((menu, key) => (
                    <li key={key}>
                        <Link to={menu.path}>
                            <div className="menu-icon" dangerouslySetInnerHTML={{ __html: menu.icon }}></div>
                            <span className="nav-text">{menu.text}</span>
                        </Link>
                    </li>
                )) : null
            }
        </MetisMenu>
    );
}

export default AsideMenu;