import axiosClient from "./axiosClient";
import AppCookie from '../helpers/AppCookie';

class VietQrModel {
    constructor(){
        this.api_url = "https://api.vietqr.io/v2/banks";
    }

    async getAllVietQR() {
        const res = await axiosClient.get(this.api_url);
        return res;
    }

}

export default new VietQrModel;